@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@keyframes opacityToggle {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 0.3;
  }
  60% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}
.main-nav {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1150px;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
  }
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
}
.main-nav__list--store-nav {
  display: none;
}
@media (min-width: 1150px) {
  .main-nav__list--store-nav {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    width: 50%;
    order: 3;
  }
  .main-nav__list--store-nav .main-nav__item {
    padding: 0;
  }
  .main-nav__list--store-nav .main-nav__link {
    display: block;
    padding: 18px 12px 18px 50px;
    font-size: 15px;
    line-height: 24px;
  }
}
@media (min-width: 768px) {
  .main-nav__list--user-nav {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: flex-end;
    order: 1;
    width: 75%;
    padding-right: 0;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .main-nav__list--user-nav .main-nav__item {
    padding: 0;
    border-left: 1px solid #ececec;
  }
  .main-nav__list--user-nav .main-nav__item--cart {
    width: 316px;
  }
  .main-nav__list--user-nav .main-nav__item--search {
    width: 82px;
  }
}
@media (min-width: 1150px) {
  .main-nav__list--user-nav {
    position: relative;
    justify-content: flex-end;
    align-items: flex-start;
    width: 500px;
    order: 2;
  }
}
.main-nav__list--user-nav .main-nav__link--search::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
}
@media (min-width: 768px) {
  .main-nav__list--user-nav .main-nav__link--search {
    display: block;
    padding: 45px 0 44px 82px;
  }
}
.main-nav__list--user-nav .main-nav__link--cart::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
}
@media (min-width: 768px) {
  .main-nav__list--user-nav .main-nav__link--cart {
    display: block;
    padding: 32px 30px 32px 83px;
  }
}
@media (min-width: 768px) {
  .main-nav__list--site-nav {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding-left: 25px;
    width: 55%;
    order: 2;
  }
  .main-nav__list--site-nav .main-nav__item {
    padding: 0;
    width: 168px;
  }
  .main-nav__list--site-nav .main-nav__link {
    display: block;
    padding: 17px 7px 19px 24px;
    font-size: 15px;
    line-height: 24px;
  }
}
@media (min-width: 1150px) {
  .main-nav__list--site-nav {
    padding-left: 11px;
    width: 500px;
    order: 0;
  }
  .main-nav__list--site-nav .main-nav__item {
    padding: 0;
    width: 224px;
    text-align: left;
    border-right: 1px solid #ececec;
  }
  .main-nav__list--site-nav .main-nav__link {
    padding: 33px;
    font-size: 17px;
    line-height: 24px;
    text-align: center;
  }
}
.main-nav__item {
  position: relative;
  margin-top: -1px;
  padding: 26px 20px 28px 74px;
}
@media (max-width: 767px) {
  .main-nav__item {
    border: 1px solid #ececec;
  }
}
.main-nav__link {
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  transition: 0.3s;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:hover .main-nav__link-icon {
  fill: #63d1bb;
  opacity: 1;
}
.main-nav__link:hover .main-nav__link-cart-text {
  opacity: 0.6;
}
.main-nav__link:focus {
  outline: dashed 2px #c6c6c6;
  outline-offset: 2px;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__link:active .main-nav__link-icon {
  fill: #63d1bb;
  opacity: 0.3;
}
.main-nav__link:active .main-nav__link-cart-text {
  opacity: 0.3;
}
.main-nav__link-icon {
  position: absolute;
  left: 30px;
  top: 30px;
  opacity: 0.2;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .main-nav__link-icon {
    left: 32px;
    top: 36px;
  }
}
@media (max-width: 767px) {
  .main-nav__text {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__text {
    padding-right: 50px;
    margin-top: 16px;
    margin-left: auto;
    max-width: 40%;
    order: 4;
    text-align: right;
  }
}
@media (min-width: 1150px) {
  .main-nav__text {
    max-width: 35%;
  }
}
.main-nav__link-search-text,
.main-nav__link-cart-text {
  transition: 0.3s;
}
@media (min-width: 768px) {
  .main-nav__link-search-text {
    display: none;
  }
}
.main-nav__logo-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  padding-top: 21px;
  padding-bottom: 23px;
}
@media (max-width: 767px) {
  .main-nav__logo-wrapper {
    border-bottom: 1px solid #ececec;
  }
}
@media (min-width: 768px) {
  .main-nav__logo-wrapper {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    width: 25%;
    padding-left: 49px;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo-wrapper {
    flex-grow: 0;
    justify-content: flex-start;
    width: 150px;
    order: 1;
    padding: 8px 0 0 0;
  }
}
.main-nav__logo {
  position: relative;
  display: block;
  transition: 0.3s;
}
.main-nav__logo::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__logo:focus {
  outline: dashed 2px #c6c6c6;
  outline-offset: 2px;
}
.main-nav__logo:active {
  opacity: 0.3;
}
.main-nav__logo-image {
  width: 86px;
  height: 35px;
}
@media (min-width: 768px) {
  .main-nav__logo-image {
    width: 112px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo-image {
    width: 149px;
    height: 72px;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 31px;
  right: 27px;
  width: 24px;
  height: 18px;
  background-color: #ffffff;
  border: none;
  display: block;
  cursor: pointer;
  background-image: url("../img/menu-open-icon.svg");
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle {
  top: 29px;
  right: 30px;
  width: 21px;
  height: 20px;
  background-image: url("../img/menu-close-icon.svg");
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__list--site-nav,
  .main-nav--opened .main-nav__list--user-nav {
    display: block;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list--site-nav,
  .main-nav--closed .main-nav__list--user-nav {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__list--site-nav,
  .main-nav--nojs .main-nav__list--user-nav {
    display: block;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
.button {
  display: block;
  padding: 13px 10px;
  width: 100%;
  min-height: 52px;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  color: #444444;
  border: 2px solid #444444;
  background-color: #ffffff;
  transition: 0.3s;
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
}
.button:focus {
  outline: dashed 2px #c6c6c6;
  outline-offset: 3px;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}
.container {
  min-width: 320px;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    margin: 0 auto;
    max-width: 1150px;
  }
}
.promo {
  background-color: #f9f9f9;
}
@media (min-width: 768px) {
  .promo {
    position: relative;
  }
  .promo::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 24px;
    background-image: url("../img/bg-zigzag-fill.svg");
    background-size: 770px 23px;
    background-repeat: repeat-x;
    background-position: bottom;
  }
}
@media (min-width: 1150px) {
  .promo {
    margin: 0 auto;
  }
  .promo::after {
    height: 38px;
    background-size: 1250px 35px;
  }
}
.promo__wrapper {
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center calc(100% - 134px);
  width: 100%;
  padding-top: 50px;
}
@media (min-resolution: 144dpi), (min-resolution: 2dppx) {
  .promo__wrapper {
    background-image: url("../img/hero-mobile@2x.jpg");
