  }
}
@media (min-width: 768px) {
  .promo__wrapper {
    padding-left: 50px;
    padding-right: 50px;
    max-width: 1150px;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-position: center calc(100% - 8px);
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 2dppx) {
