    background-image: url("../img/hero-tablet@2x.jpg");
    background-size: 1153px auto;
  }
}
@media (min-width: 1150px) {
  .promo__wrapper {
    margin: 0 auto;
    background-image: url("../img/hero-desktop@1x.jpg");
    background-position: center calc(100% - 18px);
  }
}
@media (min-width: 1150px) and (min-resolution: 144dpi), (min-width: 1150px) and (min-resolution: 2dppx) {
  .promo__wrapper {
    background-image: url("../img/hero-desktop@2x.jpg");
    background-size: 1153px auto;
  }
}
.promo__title {
  margin: 0 auto;
  margin-top: 19px;
  padding: 0 0 123px 0;
